import { connect } from 'react-redux';
import { addItemInBasketAC, delItemOfBasketAC } from '../../redux/actions';
import Card from './Card';

const mapStateToProps = (state) => {
    return { basket: state.cardsPlace.basket };
}

const mapDispatchToProps = (dispatch) => {
    return {
        addItemInBasket: (id) => dispatch(addItemInBasketAC(id)),
        delItemOfBasket: (id) => dispatch(delItemOfBasketAC(id))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Card);
