import React from 'react';
import s from './Basket.module.css';
import { NavLink } from 'react-router-dom';
import BasketCard from './BasketCard/BasketCard';

export default class Basket extends React.Component {
    render() {
        let cards = [];
        let totalPrice = 0;

        this.props.cards.forEach(element => {
            if (this.props.basket.indexOf(element.id) !== -1) {
                cards.push(element);
                totalPrice += element.price;
            }
        });
        
        return (
            <div className={ s.basket }>
                <div className={ s.basket__container }>
                    <div className={ s.basket__bar }>
                        <div>üîî –û–±—â–∞—è —Å—É–º–º–∞: { totalPrice } —Ä—É–±.</div>
                        <NavLink to="/" className={ s.basket__close }></NavLink>
                    </div>
                    <tbody>
                        { cards.map(card => <BasketCard key={ card.id } { ...card } delItemOfBasket={ this.props.delItemOfBasket }/>) }
                    </tbody>
                </div>
            </div>
        );
    }
}