import React from 'react';
import s from './CardPlace.module.css';
import CardContainer from '../Card/CardContainer';
import Preloader from '../Preloader/Preloader';
import Failed from '../Failed/Failed';


export default class CardPlace extends React.Component {
    render() {
        return (
            <>
                {
                    this.props.failed
                        ? <Failed />
                        : this.props.isFetching
                            ? <Preloader />
                            : <div className={ s.cardPlace }>
                                { this.props.cards.map((card) =>  <CardContainer key={ card.id } { ...card } />) }
                            </div>
                }
            
            </>
            
        );
    }
}