import React from 'react';
import s from './BasketCard.module.css';

export default class BasketCard extends React.Component {
    render() {
        const { id, name, price, delItemOfBasket } = this.props;

        return (
        <tr className={ s.basketCard }>
            <td>
                <div className={ s.basketCard__name }>‚ö° { name }</div>
            </td>
            <td>
                <div className={ s.basketCard__price }>{ price } —Ä—É–±.</div>
            </td>
            <td>
                <div className={ s.basketCard__btn } onClick={ () => delItemOfBasket(id) }></div>
            </td>
        </tr>
        );
    }
}