import React from 'react';
import s from './Card.module.css';

export default class Card extends React.Component {
    render() {
        return (
            <div className={ s.card }>
                <div className={ s.card__name }>{ this.props.name }</div>
                <img className={ s.card__img } src={ this.props.img } alt=""/>
                <div className={ s.card__price }><span role="img" aria-label="">‚ö°</span> { this.props.price } —Ä—É–±.</div>
                {
                    this.props.basket.indexOf(this.props.id) !== -1
                    ? <button className={s.card__btn_select} onClick={ () => this.props.delItemOfBasket(this.props.id)}>–£–¥–∞–ª–∏—Ç—å –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã</button>
                    : <button className={s.card__btn_unselect} onClick={ () => this.props.addItemInBasket(this.props.id)}>–î–æ–±–∞–≤–∏—Ç—å –≤ –∫–æ—Ä–∑–∏–Ω—É</button>
                }
            </div>
        );
    }
}