import React from 'react';
import { connect } from 'react-redux';
import CardPlace from './CardPlace';
import { setCardsAC, toggleFetchAC, setFailedAC } from '../../redux/actions';
import * as axios from 'axios';

class CardPlaceContainer extends React.Component {
    componentDidMount() {
        this.props.toggleFetch(true);
        axios.get('https://gist.githubusercontent.com/TenTax/feecd89f8752ec781b52a4ad328ab45e/raw/8a46d89d7a60631461ccbac7fe34431a9e65e688/guitarShop.json')
            .then(res => {
                this.props.setCards(res.data);
                this.props.toggleFetch(false);
            })
            .catch(err => {
                console.log(err);
                this.props.setFailed(true);
            })
    }
    render() {
        return <CardPlace cards={ this.props.cards } isFetching={ this.props.isFetching } failed={ this.props.failed } />;
    }
}

const mapStateToProps = (state) => {
    return { 
        cards: state.cardsPlace.cards,
        isFetching: state.cardsPlace.isFetching,
        failed: state.cardsPlace.failed
    };
}

const mapDispatchToProps = (dispatch) => {
    return { 
        setCards: (cards) => dispatch(setCardsAC(cards)),
        toggleFetch: (toggle) => dispatch(toggleFetchAC(toggle)),
        setFailed: (toogle) => dispatch(setFailedAC(toogle))
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(CardPlaceContainer);
