import {
    ADD_ITEM_IN_BASKET, 
    DEL_ITEM_OF_BASKET, 
    SET_CARDS,
    TOGGLE_FETCH,
    SET_FAILED
} from '../actionTypes';

const initialState = {
    cards: [],
    isFetching: false,
    failed: false,
    basket: Boolean(localStorage.getItem('basket')) ? JSON.parse(localStorage.getItem('basket')) : []
}

const cardsPlaceReducer = (state = initialState, action) => {
    switch(action.type) {
        case SET_CARDS:
            return {
                ...state,
                cards: action.cards
            }

        case ADD_ITEM_IN_BASKET: {
            let basketCopy = [...state.basket, action.itemId];

            localStorage.setItem('basket', JSON.stringify(basketCopy));
            basketCopy = JSON.parse(localStorage.getItem('basket'));

            let stateCopy = {
                ...state,
                basket: basketCopy
            }

            return stateCopy;
        }

        case DEL_ITEM_OF_BASKET: {
            let basketCopy = [...state.basket];
            const index = basketCopy.indexOf(action.itemId);
            basketCopy.splice(index, 1);

            localStorage.setItem('basket', JSON.stringify(basketCopy));
            basketCopy = JSON.parse(localStorage.getItem('basket'));

            let stateCopy = {
                ...state,
                basket: basketCopy
            }

            return stateCopy;
        }

        case TOGGLE_FETCH: {
            return {
                ...state,
                isFetching: action.toggle
            }
        }

        case SET_FAILED: {
            return {
                ...state,
                failed: action.toggle
            }
        }

        default:
            return state;
    }
}

export default cardsPlaceReducer;