import {
    ADD_ITEM_IN_BASKET,
    DEL_ITEM_OF_BASKET,
    SET_CARDS,
    TOGGLE_FETCH,
    SET_FAILED
} from './actionTypes';

export const addItemInBasketAC = (id) => {
    return {
       type: ADD_ITEM_IN_BASKET,
       itemId: id
    }
}

export const delItemOfBasketAC = (id) => {
    return {
       type: DEL_ITEM_OF_BASKET,
       itemId: id
    }
}

export const setCardsAC = (cards) => {
    return {
       type: SET_CARDS,
       cards: cards
    }
}

export const toggleFetchAC = (toggle) => {
    return {
       type: TOGGLE_FETCH,
       toggle: toggle
    }
}

export const setFailedAC = (toggle) => {
    return {
       type: SET_FAILED,
       toggle: toggle
    }
}